@media screen and (max-width: 767.5px) {
  .f_first_half {
    height: 40% !important; }
  .letstalk_div {
    margin-left: 0 !important;
    font-size: 70px !important; }
  .lets {
    opacity: 1 !important;
    color: white !important;
    margin-right: 25px !important; }
  .talk {
    opacity: 1 !important;
    color: white !important; }
  .f_sec_half {
    top: 40% !important;
    height: 60% !important; }
  .ffh_sayHello {
    bottom: -100px !important;
    height: 150px !important;
    font-size: 40px !important;
    opacity: 1 !important; }
  .ffh_sayHello span {
    color: red !important; }
  .slide_ff_line {
    bottom: 115px !important; }
  .ffh_sayHello:hover .slide_ff_line {
    width: 250px !important; }
  .fourth_no_page {
    top: 150px !important;
    font-size: 13px !important; }
  .wrap_cont_det {
    height: 100% !important; }
  .wrap_social_txt {
    top: 20px !important;
    left: 10px !important; }
  .copyrights {
    bottom: -15px !important; }
  .wrap_social {
    padding-left: 50px !important;
    bottom: 5px !important; }
  .wrap_social_icons {
    bottom: -10px !important;
    padding-left: 65px !important; } }

.container-fluid {
  position: absolute;
  height: 100%;
  margin: 0;
  padding: 0;
  display: block; }

.f_first_half {
  top: 0;
  left: 0;
  display: block;
  height: 100%;
  position: absolute;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: black; }

.f_sec_half {
  top: 0;
  right: 0;
  display: block;
  height: 100%;
  padding: 0;
  margin: 0;
  position: absolute;
  overflow: hidden;
  background-color: white; }

.letstalk_div {
  position: absolute;
  z-index: 1050;
  display: block;
  font-size: 80px;
  color: black;
  top: 10%;
  margin-left: -40px;
  font-family: 'Righteous', cursive; }

.letstalk_div .lets {
  color: white;
  margin-right: 325px;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  opacity: 0; }

.talk {
  opacity: 0; }

.letstalk_div p span:nth-child(2) {
  color: black; }

.wrap_cont_det {
  height: 60%;
  display: block;
  position: absolute;
  bottom: 50px;
  left: 0; }

.ffh_sayHello {
  display: block;
  position: absolute;
  height: 100px;
  bottom: 100px;
  right: 0;
  opacity: 0;
  -webkit-transition: 2s;
  -o-transition: 2s;
  transition: 2s;
  font-size: 55px;
  color: white;
  font-family: 'Archivo Black', sans-serif;
  overflow: hidden; }

.ffh_sayHello span {
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  opacity: 0;
  margin-left: 50px; }

.slide_ff_line {
  display: block;
  position: absolute;
  bottom: 50px;
  left: 0;
  background-color: white;
  width: 0;
  height: 7px;
  -webkit-transition: 0.7s;
  -o-transition: 0.7s;
  transition: 0.7s; }

.ffh_sayHello:hover {
  opacity: 0.9; }

.ffh_sayHello:hover .slide_ff_line {
  width: 300px; }

.fourth_no_page {
  position: absolute;
  top: 525px;
  left: 0;
  color: #FFFFFF;
  width: 20px;
  height: 80px;
  display: inline-block;
  z-index: 1060;
  font-size: 15px;
  opacity: 0.6; }

.fourth_no_page span {
  display: block;
  height: 2px;
  width: 30px;
  margin: 15px 5px 15px 5px;
  background-color: white;
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg); }

.wrap_social_txt {
  max-height: 160px;
  min-width: 90%;
  width: 70%;
  left: 30px;
  top: -10px;
  display: block;
  position: absolute;
  padding: 5px;
  color: black;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  opacity: 0;
  padding-top: 50px;
  text-align: left;
  word-wrap: break-word;
  overflow: hidden;
  font-family: 'Archivo Black', sans-serif; }

.wrap_social {
  height: 55%;
  width: 250px;
  right: 30px;
  bottom: 0;
  display: block;
  position: absolute;
  color: black;
  padding-left: 30px;
  margin: 0;
  overflow: hidden;
  font-family: 'Open Sans', sans-serif; }

.wrap_social p {
  position: relative;
  left: 20px; }

.wrap_social a {
  text-decoration: underline;
  color: black; }

.wrap_social .connect_txt {
  margin-bottom: 20px;
  font-weight: bold;
  color: red; }

.wrap_social_icons {
  display: block;
  height: 20%;
  width: 100%;
  position: absolute;
  left: 0;
  padding-left: 45px;
  bottom: 0;
  display: inline-block; }

.wrap_social_icons span {
  width: 30px;
  height: 30px;
  display: inline-block;
  margin-right: 25px;
  -webkit-transition: 0.7s;
  -o-transition: 0.7s;
  transition: 0.7s;
  opacity: 1;
  cursor: pointer; }

.wrap_social_icons span:hover {
  opacity: 0.8; }

.wrap_social_icons .facebook_icon {
  background-image: url("../../images/social_icon/facebook.png");
  background-size: cover;
  background-repeat: no-repeat; }

.wrap_social_icons .github_icon {
  background-image: url("../../images/social_icon/github.png");
  background-size: cover;
  background-repeat: no-repeat; }

.wrap_social_icons .linkedin_icon {
  background-image: url("../../images/social_icon/linkedin.png");
  background-size: cover;
  background-repeat: no-repeat; }

.copyrights {
  position: absolute;
  right: 5px;
  bottom: 30px;
  -webkit-transform-origin: right bottom;
  -ms-transform-origin: right bottom;
  transform-origin: right bottom;
  font-size: 13px;
  font-weight: bold;
  -webkit-transform: rotate(-90deg) translateX(15rem);
  -ms-transform: rotate(-90deg) translateX(15rem);
  transform: rotate(-90deg) translateX(15rem);
  width: 300px;
  text-align: right;
  overflow: hidden; }
