html,
body {
  height: 100%;
  width: 100%; }

.container-fluid {
  position: absolute;
  height: 100%;
  margin: 0;
  padding: 0;
  display: block; }

.t_first_half {
  top: 0;
  left: 0;
  display: block;
  height: 100%;
  position: absolute;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: black; }

.img_wrapper {
  position: absolute;
  display: block;
  top: 0;
  height: 100%;
  left: 0;
  margin: 0;
  padding: 0; }

.t_sec_half {
  top: 0;
  right: 0;
  display: block;
  height: 100%;
  position: absolute;
  overflow: hidden; }

.tsh_half_dark {
  height: 50%;
  position: absolute;
  right: 0;
  bottom: 0;
  display: block;
  background: -webkit-gradient(linear, left top, right top, color-stop(50%, white), color-stop(50%, black));
  background: -webkit-linear-gradient(left, white 50%, black 50%);
  background: -o-linear-gradient(left, white 50%, black 50%);
  background: linear-gradient(to right, white 50%, black 50%);
  background-size: 200% 100%;
  background-position: left bottom;
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  cursor: pointer; }

.tsh_half_dark:hover {
  background-position: right bottom; }

.helping_txt,
.newbrands_txt,
.startup_txt {
  font-family: 'Righteous', cursive;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none; }

.helping_txt {
  top: 70px;
  font-size: 80px;
  color: black;
  left: 40px;
  position: relative;
  font-weight: bold;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  pointer-events: none;
  z-index: 1; }

.newbrands_txt {
  position: absolute;
  font-size: 80px;
  top: 140px;
  padding-left: 40px;
  color: black;
  font-weight: bold;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s; }

.newbrands_txt span {
  background-color: red;
  z-index: -1; }

.startup_txt {
  position: absolute;
  font-size: 80px;
  top: 380px;
  z-index: 1060;
  left: 40px;
  color: white;
  font-weight: bold;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none; }

.oldOnes_txt,
.startOver_txt {
  font-size: 90px;
  font-family: 'Righteous', cursive;
  font-weight: bold;
  position: absolute;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none; }

.and_txt {
  top: -240px;
  position: absolute;
  left: 40px;
  font-size: 130px;
  -webkit-transition: 0.1s;
  -o-transition: 0.1s;
  transition: 0.1s;
  font-family: 'Luckiest Guy', cursive;
  color: black;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none; }

.oldOnes_txt {
  top: 140px;
  font-weight: bold;
  width: 100%;
  left: -600px;
  color: black;
  padding-left: 40px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  font-size: 80px;
  z-index: 1; }

.startOver_txt {
  position: absolute;
  font-size: 80px;
  top: 380px;
  z-index: 1060;
  left: 40px;
  color: white;
  font-weight: bold;
  opacity: 0;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  -webkit-user-select: none;
  -moz-user-select: none;
  -khtml-user-select: none;
  -ms-user-select: none; }

.startOver_txt span {
  text-decoration: underline; }

.third_no_page {
  position: absolute;
  top: 630px;
  right: 0;
  color: white;
  width: 20px;
  height: 80px;
  display: inline-block;
  z-index: 1060;
  font-size: 15px;
  opacity: 0.6; }

.third_no_page span {
  display: block;
  height: 2px;
  width: 30px;
  margin: 15px 5px 15px 5px;
  background-color: white;
  -webkit-transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  transform: rotate(90deg); }
