html,
body {
  position: relative;
  height: 100%;
  overflow-y: hidden; }

body {
  padding: 0;
  margin: 0;
  -webkit-transition: 2s ease-in-out;
  -o-transition: 2s ease-in-out;
  transition: 2s ease-in-out;
  background-color: #000000; }

.swiper-container {
  width: 100%;
  min-height: 100%;
  max-height: auto;
  position: absolute;
  margin: 0;
  padding: 0; }

.swiper-slide {
  max-height: auto;
  overflow: hidden;
  position: absolute; }

.firstpage,
.secondpage,
.thirdpage,
.fourthpage {
  margin: 0;
  padding: 0;
  position: relative;
  overflow: hidden;
  display: table; }

.firstpage {
  background-color: #000000;
  z-index: 999;
  display: block; }

.secondpage {
  background-color: #000000;
  z-index: 1000;
  display: block; }

.thirdpage {
  background-color: white;
  display: block; }

.fourthpage {
  display: block; }

@media screen and (max-width: 500px) {
  .wrap_menu_list p:nth-child(1),
  p:nth-child(2),
  p:nth-child(5) {
    font-size: 10vw !important; }
  .wrap_menu_list .menu_github,
  .menu_portfolio {
    font-size: 14px !important;
    margin-top: 2px !important; }
  .menu_social_icons {
    top: 70% !important; }
  .m_soc_github {
    width: 45px !important;
    height: 45px !important;
    margin: 20px !important; }
  .m_soc_linkedin,
  .m_soc_web {
    width: 40px !important;
    height: 40px !important;
    margin: 20px !important; }
  .p_contact {
    margin-top: 10% !important; }
  .p_contact span {
    bottom: 40px !important; }
  .menu_github span {
    bottom: 190px !important; }
  .menu_portfolio span {
    bottom: 150px !important; } }

@media screen and (min-width: 500px) and (max-width: 991px) {
  .wrap_menu_list p:nth-child(1),
  p:nth-child(2),
  p:nth-child(5) {
    font-size: 6vw !important; }
  .p_contact {
    margin-top: 10% !important; } }

@media screen and (max-width: 520px) {
  .cursor {
    display: none !important; } }

.slide_menu {
  display: block;
  position: absolute;
  z-index: 1080;
  width: 0;
  height: 100%;
  right: 0;
  top: 0;
  overflow: hidden;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  background-color: rgba(0, 0, 0, 0.95); }

.wrap_menu_list {
  display: block;
  text-align: left;
  position: absolute;
  top: 50px;
  overflow: hidden; }

.wrap_menu_list p {
  font-size: 60px;
  font-family: 'Roboto Slab', serif;
  font-weight: bold;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
  cursor: pointer; }

.wrap_menu_list a {
  text-decoration: none; }

.wrap_menu_list p:hover {
  opacity: 0.8; }

.wrap_menu_list p:nth-child(1) {
  color: white; }

.p_contact {
  margin-top: 30px;
  color: white; }

.menu_work {
  color: white; }

.wrap_menu_list .menu_github,
.menu_portfolio {
  font-size: 19px !important;
  margin-top: 2px !important;
  color: red !important;
  cursor: default;
  width: 100px; }

.menu_github a,
.menu_portfolio a {
  color: red !important; }

.menu_github {
  -webkit-transition: 1s;
  -o-transition: 1s;
  transition: 1s;
  margin-left: -120px; }

.menu_portfolio {
  -webkit-transition: 2s;
  -o-transition: 2s;
  transition: 2s;
  margin-left: -120px; }

.toggleMenuWork {
  margin-left: 5px !important; }

.menu_social_icons {
  display: inline-block;
  position: absolute;
  height: 200px;
  border-top: solid #991010;
  padding-top: 25px;
  bottom: 0; }

.menu_social_icons span {
  width: 40px;
  height: 40px;
  margin: 40px;
  display: inline-block;
  border-radius: 20px;
  opacity: 0.8;
  -webkit-transition: .1s;
  -o-transition: .1s;
  transition: .1s; }

.menu_social_icons span:hover {
  opacity: 1; }

.menu_social_icons .m_soc_github {
  background-image: url("../images/social_icon/m_github.png");
  background-size: cover;
  background-repeat: no-repeat;
  width: 45px;
  height: 45px; }

.menu_social_icons .m_soc_linkedin {
  background-image: url("../images/social_icon/m_linkedin.png");
  background-size: cover;
  background-repeat: no-repeat; }

.menu_social_icons .m_soc_web {
  background-image: url("../images/social_icon/m_website.png");
  background-size: cover;
  background-repeat: no-repeat; }

.m_soc_github {
  /*  animation: github_bounce 0.5s;*/
  -webkit-animation-delay: 0.2s;
  animation-delay: 0.2s; }

@-webkit-keyframes github_bounce {
  from {
    -webkit-transform: scale(1.1);
    transform: scale(1.1); }
  to {
    -webkit-transform: scale(1.3);
    transform: scale(1.3); } }

@keyframes github_bounce {
  from {
    -webkit-transform: scale(1.1);
    transform: scale(1.1); }
  to {
    -webkit-transform: scale(1.3);
    transform: scale(1.3); } }

.m_soc_linkedin {
  /* animation: linkedin_bounce 1s;*/
  -webkit-animation-delay: 0.3s;
  animation-delay: 0.3s; }

@-webkit-keyframes linkedin_bounce {
  from {
    -webkit-transform: scale(1.1);
    transform: scale(1.1); }
  to {
    -webkit-transform: scale(1.3);
    transform: scale(1.3); } }

@keyframes linkedin_bounce {
  from {
    -webkit-transform: scale(1.1);
    transform: scale(1.1); }
  to {
    -webkit-transform: scale(1.3);
    transform: scale(1.3); } }

.m_soc_web {
  -webkit-animation-delay: 0.4s;
  animation-delay: 0.4s; }

@-webkit-keyframes m_soc_web_bounce {
  from {
    -webkit-transform: scale(1.1);
    transform: scale(1.1); }
  to {
    -webkit-transform: scale(1.3);
    transform: scale(1.3); } }

@keyframes m_soc_web_bounce {
  from {
    -webkit-transform: scale(1.1);
    transform: scale(1.1); }
  to {
    -webkit-transform: scale(1.3);
    transform: scale(1.3); } }

.p_contact span {
  display: block;
  position: absolute;
  width: 0;
  height: 8px;
  background-color: red;
  bottom: 50px;
  -webkit-transition: .7s;
  -o-transition: .7s;
  transition: .7s;
  border-radius: 10px; }

.p_contact:hover span {
  width: 100%; }

.menu_email_contact {
  opacity: 0.6;
  color: white;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s; }

.close_menu {
  display: block;
  height: 60px;
  width: 60px;
  position: absolute;
  right: 10px;
  top: 10px;
  color: white;
  font-size: 70px;
  line-height: 38px;
  cursor: pointer; }

.close_menu span {
  position: absolute; }

.close_menu:hover {
  opacity: 0.8; }

.toggMenu {
  width: 100%; }

.cursor {
  width: 30px;
  height: 30px;
  border: 2px solid white;
  border-radius: 50%;
  position: absolute;
  display: block;
  -webkit-transition-timing-function: ease-out;
  -o-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
  -webkit-animation: cursorAnim 1s infinite alternate;
  animation: cursorAnim 1s infinite alternate;
  pointer-events: none;
  z-index: 2000; }

.cursor::after {
  content: "";
  width: 25px;
  height: 25px;
  position: absolute;
  border: 5px solid red;
  border-radius: 50%;
  opacity: .8;
  top: 1px;
  left: 1px; }

@-webkit-keyframes cursorAnim {
  from {
    -webkit-transform: scale(1);
    transform: scale(1); }
  to {
    -webkit-transform: scale(0.9);
    transform: scale(0.9); } }

@keyframes cursorAnim {
  from {
    -webkit-transform: scale(1);
    transform: scale(1); }
  to {
    -webkit-transform: scale(0.9);
    transform: scale(0.9); } }

.loading_div {
  position: absolute;
  display: block;
  background-color: black;
  z-index: 2000;
  width: 100%;
  height: 100%;
  margin: 0;
  top: 0;
  right: 0;
  overflow: hidden;
  -webkit-transition: 0.8s;
  -o-transition: 0.8s;
  transition: 0.8s; }

.load_logo_wrapper {
  display: block;
  position: absolute;
  height: 200px;
  width: 200px;
  top: 40%;
  -webkit-transition: 1s ease-out;
  -o-transition: 1s ease-out;
  transition: 1s ease-out;
  -webkit-animation: wrapp_load_logo 3s ease-out infinite;
  animation: wrapp_load_logo 3s ease-out infinite; }

@-webkit-keyframes wrapp_load_logo {
  from {
    opacity: 1; }
  to {
    opacity: 0.9; } }

@keyframes wrapp_load_logo {
  from {
    opacity: 1; }
  to {
    opacity: 0.9; } }

.llw_first {
  position: absolute;
  display: block;
  height: 130px;
  width: 130px;
  top: 20px;
  border: solid white;
  border-radius: 100px;
  background: -webkit-linear-gradient(45deg, transparent 40%, red 90%);
  background: -o-linear-gradient(45deg, transparent 40%, red 90%);
  background: linear-gradient(45deg, transparent 40%, red 90%);
  -webkit-animation: rotate_loading 3s linear infinite;
  animation: rotate_loading 3s linear infinite; }

@-webkit-keyframes rotate_loading {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0);
    -webkit-box-shadow: 1px 0 2px 1px red;
    box-shadow: 1px 0 2px 1px red; }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
    -webkit-box-shadow: 1px 0 3px 0 red;
    box-shadow: 1px 0 3px 0 red; } }

@keyframes rotate_loading {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0);
    -webkit-box-shadow: 1px 0 2px 1px red;
    box-shadow: 1px 0 2px 1px red; }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
    -webkit-box-shadow: 1px 0 3px 0 red;
    box-shadow: 1px 0 3px 0 red; } }

.llw_sec {
  position: absolute;
  display: block;
  height: 110px;
  width: 110px;
  top: 30px;
  border-radius: 100px;
  background-color: black;
  -webkit-box-shadow: 1px 0 4px 0 black;
  box-shadow: 1px 0 4px 0 black;
  /*background: linear-gradient(75deg, transparent 85%, white 50%);*/
  -webkit-animation: rotate_loading_two 5s linear infinite;
  animation: rotate_loading_two 5s linear infinite; }

@-webkit-keyframes rotate_loading_two {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0); }
  to {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg); } }

@keyframes rotate_loading_two {
  from {
    -webkit-transform: rotate(0);
    transform: rotate(0); }
  to {
    -webkit-transform: rotate(-360deg);
    transform: rotate(-360deg); } }

.llw_sec span {
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: red;
  left: -40px;
  border-radius: 70% 30% 30% 70% / 70% 70% 30% 30%;
  -webkit-animation: small_load_eff_mod 10s linear infinite;
  animation: small_load_eff_mod 10s linear infinite;
  opacity: 0.7; }

@-webkit-keyframes small_load_eff_mod {
  0% {
    border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
    width: 25px;
    height: 25px; }
  25% {
    border-radius: 58% 42% 75% 25% / 76% 46% 54% 24%; }
  50% {
    border-radius: 50% 50% 33% 67% / 55% 27% 73% 45%; }
  75% {
    border-radius: 33% 67% 58% 42% / 63% 68% 32% 37%; }
  100% {
    border-radius: 45% 55% 75% 30% / 76% 55% 45% 24%; } }

@keyframes small_load_eff_mod {
  0% {
    border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
    width: 25px;
    height: 25px; }
  25% {
    border-radius: 58% 42% 75% 25% / 76% 46% 54% 24%; }
  50% {
    border-radius: 50% 50% 33% 67% / 55% 27% 73% 45%; }
  75% {
    border-radius: 33% 67% 58% 42% / 63% 68% 32% 37%; }
  100% {
    border-radius: 45% 55% 75% 30% / 76% 55% 45% 24%; } }

.loading_txt {
  position: relative;
  display: block;
  height: 100px;
  width: 100px;
  border-radius: 100px;
  top: 35px;
  text-align: center;
  padding-top: 35px;
  color: white; }

.loading_txt span:before {
  content: attr(data-text);
  position: absolute;
  overflow: hidden;
  max-width: 7em;
  white-space: nowrap;
  color: #fff;
  margin-left: -40px;
  -webkit-animation: loading 5s linear infinite;
  animation: loading 5s linear infinite; }

@-webkit-keyframes loading {
  0% {
    max-width: 0; } }

@keyframes loading {
  0% {
    max-width: 0; } }
